@import url('https://fonts.googleapis.com/css2?family=Inconsolata&display=swap');
$brown-color: #271608;
$pink-color: #B53389;
$white-color: #fff;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #C36F29;
  height: 100%;
  font-family: 'Inconsolata', monospace;

  .App {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    .container {
      width: 350px;
      height: 400px;
      background-color: $brown-color;

      .displaybox {
        width: 100%;
        height: 60px;
        color: $white-color;
        text-align: right;
        word-wrap: break-word;
        word-break: break-all;
        overflow-y: hidden;
        vertical-align: middle;
        font-size: 1.5rem;

        input {
          width: 100%;
          padding: 0 10px 0 0;
          text-align: right;
          background-color: $brown-color;
          border: none;
        }

        .expression {
          color: $pink-color;
          font-size: 16px;
        }

        .expression::placeholder {
          color: $pink-color;
        }
        
        .answer {
          padding: 5px 10px;
          color: $white-color;
          font-size: 24px;
        }
      }

      .boxes {
        width: 100%;
        height: 340px;
        background-color: $brown-color;
        display: grid;
        grid-template-columns: repeat(4, 84px);
        grid-template-rows: repeat(4, 65px);
        gap: 1px;
        border: 6px solid $brown-color;
    
        .box {
          background-color: #DBA97F;
          color: $brown-color;
          text-align: center;
          padding: 18px 0;
          font-size: 1.4rem;
        }
    
        .box:hover {
          color: $white-color;
          cursor: pointer;
          transform: scale(1.05, 1.05);
          border: 1.5px solid $white-color;
        }
    
        #clear {
          grid-column: 1 / 3;
          background-color: #708090;
        }
    
        #equals {
          grid-area: 4 / 4 / 6 / 4;
          line-height: 4;
          background-color: #336666;
        }
    
        #zero {
          grid-column: 2 / 4;
        }
      }

      .designer {
        margin: 7px;
        text-align: center;
        color: $white-color;
      }
    }
  }
}
